<template>
  <v-container>
    <v-layout row wrap class="mb-4">
      <v-flex xs12 sm6 class="text-xs-center text-sm-right">
        <v-btn large to="/meetups" class="accent">Explore meetups</v-btn>
      </v-flex>
      <v-flex xs12 sm6 class="text-xs-center text-sm-left">
        <v-btn large to="/meetups/new" class="accent">Organise meetups</v-btn>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex xs12 text-xs-center>
        <v-progress-circular
          indeterminate
          color="accent"
          :width="7"
          :size="70"
          v-if="loading"
        >
        </v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-if="!loading">
      <v-flex xs12>
        <v-carousel style="cursor: pointer" class="mb-4">
          <v-carousel-item
            v-for="item in meetups"
            :src="item.imageUrl"
            :key="item.id"
            @click="onLoadMeetup(item.id)"
          >
          <div class="title">{{ item.title }}</div>
          </v-carousel-item>
        </v-carousel>
      </v-flex>
      <v-layout row wrap class="mb-2">
        <v-flex xs12 class="text-xs-center">
          <p>Join our meetup</p>
        </v-flex>
      </v-layout>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data() {
      return {
      }
    },
    computed: {
      meetups() {
        return this.$store.getters.featuredMeetups;
      },
      loading() {
        return this.$store.getters.loading;
      },
    },
    methods: {
      onLoadMeetup(id) {
        this.$router.push('/meetups/' + id);
      }
    }
  }
</script>

