<template>
  <v-app>
    <v-navigation-drawer temporary absolute v-model="sideNav">
      <v-list>
        <v-list-tile
          v-for="item in menuItems"
          :key="item.title"
          :to="item.link"
          exact
        >
          <v-list-tile-action>
            <v-icon>
              {{ item.icon }}
            </v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            {{ item.name }}
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile v-if="userIsAuthenticated" @click="onLogout">
          <v-list-tile-action>
            <v-icon>
              exit_to_app
            </v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            Log out
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar dark class="primary">
      <v-toolbar-side-icon
        @click="sideNav = !sideNav"
        class="hidden-sm-and-up"
        >
      </v-toolbar-side-icon>
      <v-toolbar-title>
        <router-link
          to="/"
          tag="span"
          style="cursor: pointer"
        >
          Meetups
        </router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items
        class="hidden-xs-only"
      >
        <v-btn
          flat
          exact
          v-for="item in menuItems"
          :key="item.title"
          :to="item.link"
        >
          <v-icon left>{{ item.icon }}</v-icon>
          {{ item.name }}
        </v-btn>
        <v-btn
          v-if="userIsAuthenticated"
          @click="onLogout"
          flat
        >
          <v-icon left>exit_to_app</v-icon>
            Log out
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>
    <main>
      <router-view></router-view>
    </main>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      sideNav: false,
    }
  },
  computed: {
    userIsAuthenticated() {
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    },
    menuItems() {
      let menuItems = [
        { icon: 'face', name: 'Sign up', link: '/signup'},
        { icon: 'lock_open', name: 'Sign in', link: '/signin'},
      ]
      if (this.userIsAuthenticated) {
        menuItems = [
          { icon: 'supervisor_account', name: 'View Meetups', link: '/meetups'},
          { icon: 'room', name: 'Organise Meetup', link: '/meetups/new'},
          { icon: 'person', name: 'Profile', link: '/profile'},
        ]
       }
      return menuItems
    },
  },
  methods: {
    onLogout() {
      this.$store.dispatch('logout')
    }
  }
}
</script>
