import Vue from 'vue'
import Vuex from 'vuex'
import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loadedMeetups: [],
    user: null,
    loading: false,
    error: null,
  },
  mutations: {
    setLoadedMeetups(state, payload) {
      state.loadedMeetups = payload
    },
    createMeetup(state, payload) {
      state.loadedMeetups.push(payload);
    },
    updateMeetup(state, payload) {
      const meetup = state.loadedMeetups.find(meetup => meetup.id === payload.id)
      if (payload.title) meetup.title = payload.title
      if (payload.location) meetup.location = payload.location
      if (payload.description) meetup.description = payload.description
    },
    setUser(state, payload) {
      state.user = payload
    },
    setLoading(state, payload) {
      state.loading = payload
    },
    setError(state, payload) {
      state.error = payload
    },
    clearError(state) {
      state.error = null
    },
  },
  actions: {
    loadMeetups({ commit }){
      commit('setLoading', true)
      firebase.database().ref('meetups').once('value')
      .then( data => {
        const meetups = []
        const obj = data.val()
        for (let key in obj) {
          meetups.push({
            id: key,
            title: obj[key].title,
            description: obj[key].description,
            imageUrl: obj[key].imageUrl,
            date: obj[key].date,
            creatorId: obj[key].creatorId,
            location: obj[key].location,
          })
        }
        commit('setLoadedMeetups', meetups)
        commit('setLoading', false)
      }
      )
      .catch(error => {
        commit('setLoading', false)
        // eslint-disable-next-line
        console.error(error)
      })
    },
    createMeetup({ commit, getters }, payload) {
      const meetup = {
        title: payload.title,
        location: payload.location,
        imageUrl: payload.imageUrl,
        description: payload.description,
        date: payload.date,
        creatorId: getters.user.id,
      }
      firebase.database().ref('meetups').push(meetup)
        .then( data => {
          const key = data.key
          commit('createMeetup', {
            ...meetup,
            id: key
          })
        })
        .catch( error => {
          // eslint-disable-next-line
          console.error(error)
        })
    },
    updateMeetup({ commit }, payload){
      commit('setLoading', true)
      const updateObj = {}
      if (payload.title) updateObj.title = payload.title
      if (payload.location) updateObj.location = payload.location
      if (payload.description) updateObj.description = payload.description

      firebase.database().ref('meetups').child(payload.id).update(updateObj)
      .then( () => {
        commit('setLoading', false)
        commit('updateMeetup', payload)
      })
      .catch( error => {
        // eslint-disable-next-line
        console.error(error)
        commit('setLoading', false)
      })
    },
    signUserUp({ commit }, payload) {
      commit('setLoading', true)
      commit('clearError')
      firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
        .then(
          user => {
            commit('setLoading', false)
            const newUser = {
              id: user.user.uid,
              registeredMeetups: []
            }
            commit('setUser', newUser)
          }
        )
        .catch(
          error => {
            commit('setLoading', false)
            commit('setError', error.message)
            // eslint-disable-next-line
            console.error(error)
          }
        )
      },
    signUserIn({ commit }, payload) {
      commit('setLoading', true)
      commit('clearError')
      firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
        .then(
          user => {
            commit('setLoading', false)
            const newUser = {
              id: user.user.uid,
              registeredMeetups: []
            }
            commit('setUser', newUser)
          }
          )
          .catch(
            error => {
              commit('setError', error.message)
              commit('setLoading', false)
              // eslint-disable-next-line
              console.error(error)
            }
          )
      },
      autoSignIn({ commit }, payload) {
        commit('setUser', {id: payload.uid, registeredMeetups: []})
      },
      logout({ commit }) {
        firebase.auth().signOut()
        commit('setUser', null)
      },
      clearError({ commit }) {
        commit('clearError')
      }
  },
  getters: {
    loadedMeetups: state => state.loadedMeetups.sort((a, b) => a.date > b.date),
    loadedMeetup: state => meetupId => state.loadedMeetups.find(meetup => meetup.id == meetupId),
    featuredMeetups: getters => getters.loadedMeetups.slice(0, 5),
    user: state => state.user,
    loading: state => state.loading,
    error: state => state.error,
  },
})
