import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Meetups from './views/meetup/Meetups'
import Meetup from './views/meetup/Meetup'
import MeetupCreate from './views/meetup/MeetupCreate'
import Profile from './views/user/Profile'
import SignUp from './views/user/SignUp'
import SignIn from './views/user/SignIn'
import authGuard from './auth-guard';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/meetups',
      name: 'Meetups',
      component: Meetups
    },
    {
      path: '/meetups/new',
      name: 'MeetupCreate',
      component: MeetupCreate,
      beforeEnter: authGuard,
    },
    {
      path: '/meetups/:id',
      name: 'Meetup',
      component: Meetup,
      props: true,
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile
    },
    {
      path: '/signup',
      name: 'SignUp',
      component: SignUp
    },
    {
      path: '/signin',
      name: 'SignIn',
      component: SignIn
    },
  ]
})
