<template>
  <v-dialog
    width="350"
    persistent
    v-model="editDialog"
  >
    <v-btn
      slot="activator"
      fab
      color="accent"
    >
      <v-icon>edit</v-icon>
    </v-btn>
    <v-card>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-title>Edit Meetup</v-card-title>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-text-field
                name="title"
                label="Title"
                id="title"
                v-model="title"
              >
              </v-text-field>
              <v-text-field
                name="location"
                label="Location"
                id="location"
                v-model="location"
              >
              </v-text-field>
              <v-textarea
                name="description"
                label="Description"
                id="description"
                v-model="description"
              >
              </v-textarea>
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-btn
                flat
                @click="editDialog = false"
              >
                Cancel
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn
                flat
                class="accent--text"
                @click="onSave"
              >
                Save
              </v-btn>
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    props: [
      'meetup'
    ],
    data() {
      return {
        editDialog: false,
        title: this.meetup.title,
        location: this.meetup.location,
        description: this.meetup.description,
      }
    },
    methods: {
      onSave() {
        if (this.title.trim() === '' || this.location.trim() === '' || this.description.trim() === '') return

        this.$store.dispatch('updateMeetup', {
          id: this.meetup.id,
          title: this.title,
          location: this.location,
          description: this.description,
        })
        this.editDialog = false
      }
    }
  }
</script>
